body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background: #ececec;
}

.center {
    width: 300px;
    margin: auto;
    position: absolute;
    top: 30%;
    left: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
}


